const primaryThemes = [
  ['rgb(124,252,0)', 'rgb(124,252,0,0.07)'],
  ['rgb(79,134,247)', 'rgb(79,134,247,0.07)'],
  ['rgb(255,104,31)', 'rgb(255,104,31,0.07)'],
  ['rgb(102,255,102)', 'rgb(102,255,102,0.07)'],
  ['rgb(255,53,94)', 'rgb(255,53,94,0.07)'],
  ['rgb(201,160,220)', 'rgb(201,160,220,0.07)'],
  ['rgb(255,170,29)', 'rgb(255,170,29,0.07)'],

]
  
export function randomTheme() {
  let themeVars = primaryThemes[Math.floor(Math.random()*primaryThemes.length)];
  document.documentElement.style.setProperty('--primaryColor', themeVars[0]);
  document.documentElement.style.setProperty('--primaryBackground', themeVars[1]);
}
